import React from "react";
import cx from "classnames";

import styles from "./diagonal.scss";

export default ({ className, ...rest }) => {
  return (
    <svg
      className={cx(
        styles.wrap,
        direction == "top" ? styles.top : styles.bottom,
        className
      )}
      viewBox="0 0 200 35"
    >
      <polygon points="0,0 200,0 0,35"></polygon>
    </svg>
  );
};
